import { MEALS_TOKEN } from '../reducers/recipesReducer';

export const fetchApi = async (token, option, text = '') => {
  try {
    const url = (token === MEALS_TOKEN)
      ? 'https://www.themealdb.com/api/json/v1/1'
      : 'https://www.thecocktaildb.com/api/json/v1/1';
    let response;
    switch (option) {
    case 'name': {
      response = await fetch(`${url}/search.php?s=${text}`);
      break; }
    case 'first-letter': {
      response = await fetch(`${url}/search.php?f=${text}`);
      break; }
    case 'list': {
      response = await fetch(`${url}/search.php?s=`);
      break; }
    case 'ingredients': {
      response = await fetch(`${url}/list.php?i=list`);
      break; }
    case 'categories': {
      response = await fetch(`${url}/list.php?c=list`);
      break; }
    case 'origins': {
      response = await fetch(`${url}/list.php?a=list`);
      break; }
    case 'ingredient': {
      response = await fetch(`${url}/filter.php?i=${text}`);
      break; }
    case 'category': {
      response = await fetch(`${url}/filter.php?c=${text}`);
      break; }
    case 'origin': {
      response = await fetch(`${url}/filter.php?a=${text}`);
      break; }
    case 'id': {
      response = await fetch(`${url}/lookup.php?i=${text}`);
      break; }
    case 'random': {
      response = await fetch(`${url}/random.php`);
      break; }
    default: break;
    }
    if (!response.ok) throw new Error('Falha na requisição da fetch');
    const result = await response.json();
    return result;
  } catch (err) { throw new Error(err.message); }
};

export const getFromLocalStorage = () => {
  const doneRecipes = localStorage.getItem('doneRecipes')
    ? JSON.parse(localStorage.getItem('doneRecipes'))
    : [];
  const favoriteRecipes = localStorage.getItem('favoriteRecipes')
    ? JSON.parse(localStorage.getItem('favoriteRecipes'))
    : [];
  let inProgressRecipes;
  if (localStorage.getItem('inProgressRecipes')) {
    const obj = JSON.parse(localStorage.getItem('inProgressRecipes'));
    const { cocktails, meals } = obj;
    inProgressRecipes = { meals, drinks: { ...cocktails } };
  } else {
    inProgressRecipes = { meals: {}, drinks: {} };
  }
  return { doneRecipes, favoriteRecipes, inProgressRecipes };
};

export const saveDoneRecipesLS = (obj) => {
  const doneRecipes = localStorage.getItem('doneRecipes')
    ? JSON.parse(localStorage.getItem('doneRecipes'))
    : [];
  localStorage.setItem('doneRecipes', JSON.stringify([...doneRecipes, obj]));
};

export const saveForiteRecipesLS = (obj) => {
  const favoriteRecipes = localStorage.getItem('favoriteRecipes')
    ? JSON.parse(localStorage.getItem('favoriteRecipes'))
    : [];
  localStorage.setItem('favoriteRecipes', JSON.stringify([...favoriteRecipes, obj]));
};

export const removeFavoriteRecipeLS = (objId) => {
  const favoriteRecipes = JSON.parse(localStorage.getItem('favoriteRecipes'));
  const filtered = favoriteRecipes.filter(({ id }) => id !== objId);
  localStorage.setItem('favoriteRecipes', JSON.stringify(filtered));
};

export const saveInProgressRecipesLS = (obj, token) => {
  const inProgressRecipes = localStorage.getItem('inProgressRecipes')
    ? JSON.parse(localStorage.getItem('inProgressRecipes'))
    : { cocktails: {}, meals: {} };
  const kind = token === 'drinks' ? 'cocktails' : token;
  const newData = {
    ...inProgressRecipes,
    [kind]: { ...inProgressRecipes[kind],
      [obj.id]: [...obj.ingredientes] } };
  localStorage.setItem('inProgressRecipes', JSON.stringify(newData));
};
