import React, { useContext } from 'react';
import { useHistory } from 'react-router-dom';

import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';

import Footer from '../components/Footer';
import Header from '../components/Header';
import RecipesContext from '../context/RecipesContext';
import { getFromLocalStorage } from '../services';

function Profile() {
  const { email } = localStorage.getItem('user')
    ? JSON.parse(localStorage.getItem('user'))
    : { email: '' };
  const {
    setRecipesLS,
    recipesLS: { doneRecipes, favoriteRecipes } } = useContext(RecipesContext);
  const history = useHistory();
  return (
    <main>
      <Header title="Perfil" hasBtn={ false } />
      <div className="d-grid gap-2">
        <Alert
          variant="success"
        >
          <Alert.Heading
            data-testid="profile-email"
          >
            { email }
          </Alert.Heading>
          <hr />
          <p>
            {`Receitas Feitas: ${doneRecipes.length}`}
          </p>
          <p>
            {`Receitas Favoritas: ${favoriteRecipes.length}`}
          </p>

        </Alert>
        <Button
          data-testid="profile-done-btn"
          variant="secondary"
          size="lg"
          onClick={ () => history.push('/receitas-feitas') }
        >
          Receitas Feitas
        </Button>
        <Button
          data-testid="profile-favorite-btn"
          variant="secondary"
          size="lg"
          onClick={ () => history.push('/receitas-favoritas') }
        >
          Receitas Favoritas
        </Button>
        <Button
          data-testid="profile-logout-btn"
          variant="danger"
          size="lg"
          onClick={ () => {
            localStorage.removeItem('user');
            localStorage.removeItem('doneRecipes');
            localStorage.removeItem('favoriteRecipes');
            localStorage.removeItem('inProgressRecipes');
            localStorage.removeItem('mealsToken');
            localStorage.removeItem('cocktailsToken');
            setRecipesLS(getFromLocalStorage());
            history.push('/');
          } }
        >
          Sair
        </Button>
      </div>
      <Footer />
    </main>
  );
}

export default Profile;
