import React, { useContext, useEffect } from 'react';
import { Route, useHistory } from 'react-router-dom';
import RecipesContext from '../context/RecipesContext';
import { TOKEN, MEALS_TOKEN, DRINKS_TOKEN } from '../reducers/recipesReducer';

import {
  Login,
  Recipes,
  RecipeDetails,
  RecipeInProgress,
  Explorer,
  Profile,
  PersonalRecipes } from '../pages';

const lintAnnoyingFunction = (location) => (
  <Explorer
    title="Explorar Ingredientes"
    location={ location }
    layer="ingredients"
    hasBtn={ false }
  />);

function Routes() {
  const { recipes, dispatch } = useContext(RecipesContext);
  const { token } = recipes;
  const { location: { pathname } } = useHistory();

  useEffect(() => {
    if ((pathname.includes('comida')) && token !== MEALS_TOKEN) {
      dispatch({ type: TOKEN, token: MEALS_TOKEN });
    } else if ((pathname.includes('bebida')) && token !== DRINKS_TOKEN) {
      dispatch({ type: TOKEN, token: DRINKS_TOKEN });
    }
    // eslint-disable-next-line
  });

  return (
    <>
      <Route exact path="/"><Login /></Route>
      <Route
        exact
        path="/comidas"
        render={ () => <Recipes title="Comidas" /> }
      />
      <Route
        exact
        path="/bebidas"
        render={ () => <Recipes title="Bebidas" /> }
      />
      <Route
        exact
        path="/comidas/:id"
        render={ ({ match }) => (
          <RecipeDetails match={ match } />) }
      />
      <Route
        exact
        path="/bebidas/:id"
        render={ ({ match }) => (
          <RecipeDetails match={ match } />) }
      />
      <Route
        exact
        path="/comidas/:id/in-progress"
        render={ ({ match }) => (
          <RecipeInProgress title="Comida em pogresso" match={ match } />) }
      />
      <Route
        exact
        path="/bebidas/:id/in-progress"
        render={ ({ match }) => (
          <RecipeInProgress title="Bebida em progresso" match={ match } />) }
      />
      <Route
        exact
        path="/explorar"
        render={ ({ location }) => (
          <Explorer
            title="Explorar"
            hasBtn={ false }
            layer="initial"
            location={ location }
          />) }
      />
      <Route
        exact
        path="/explorar/comidas"
        render={ ({ location }) => (
          <Explorer
            title="Explorar Comidas"
            hasBtn={ false }
            layer="recipes"
            location={ location }
          />) }
      />
      <Route
        exact
        path="/explorar/bebidas"
        render={ ({ location }) => (
          <Explorer
            title="Explorar Bebidas"
            hasBtn={ false }
            layer="recipes"
            location={ location }
          />) }
      />
      <Route
        exact
        path="/explorar/comidas/ingredientes"
        render={ ({ location }) => lintAnnoyingFunction(location) }
      />
      <Route
        exact
        path="/explorar/bebidas/ingredientes"
        render={ ({ location }) => lintAnnoyingFunction(location) }
      />
      <Route
        exact
        path="/explorar/comidas/area"
        render={ ({ location }) => (
          <Explorer
            title="Explorar Origem"
            layer="origin"
            location={ location }
          />) }
      />
      <Route
        exact
        path="/explorar/bebidas/area"
        render={ ({ location }) => (
          <Explorer
            title="Explorar Origem"
            layer="notfound"
            location={ location }
          />) }
      />
      <Route
        exact
        path="/perfil"
      >
        <Profile />
      </Route>
      <Route
        exact
        path="/receitas-feitas"
        render={ () => (
          <PersonalRecipes
            title="Receitas Feitas"
            group="done"
          />) }
      />
      <Route
        exact
        path="/receitas-favoritas"
        render={ () => (
          <PersonalRecipes
            title="Receitas Favoritas"
            group="favorite"
          />) }
      />
    </>
  );
}

export default Routes;
