import React, { useState, useContext, useEffect } from 'react';
import PropTypes from 'prop-types';
import RecipesContext from '../context/RecipesContext';

import Header from '../components/Header';
import FilterByKind from '../components/FilterByKind';
import PersonalRecipesDetails from '../components/PersonalRecipesDetails';

function PersonalRecipes({ title, group }) {
  const { recipesLS: { doneRecipes, favoriteRecipes } } = useContext(RecipesContext);
  const [type, setType] = useState('all');
  const [recipes, setRecipes] = useState(doneRecipes);

  const handleFilter = ({ target }) => {
    const { value } = target;
    setType(value);
  };

  useEffect(() => {
    if (group === 'done') {
      const filtered = doneRecipes.filter((recipe) => {
        if (type === 'all') return true;
        return recipe.type === type;
      });
      setRecipes(filtered);
    } else {
      const filtered = favoriteRecipes.filter((recipe) => {
        if (type === 'all') return true;
        return recipe.type === type;
      });
      setRecipes(filtered);
    }
    // eslint-disable-next-line
  }, [type, doneRecipes, favoriteRecipes]);

  return (
    <main>
      <Header title={ title } hasBtn={ false } />
      <FilterByKind type={ type } handleFilter={ handleFilter } />
      { recipes.length > 0
        && <PersonalRecipesDetails recipes={ recipes } group={ group } />}
    </main>

  );
}

PersonalRecipes.propTypes = {
  title: PropTypes.string.isRequired,
  group: PropTypes.string.isRequired,
};

export default PersonalRecipes;
