import React, { useContext } from 'react';

import FloatingLabel from 'react-bootstrap/FloatingLabel';
import Form from 'react-bootstrap/Form';
import RecipesContext from '../context/RecipesContext';

import Cards from './Cards';

function OriginExplorer() {
  const { recipes, origin, handleOrigin } = useContext(RecipesContext);
  const { token } = recipes;
  const { [token]: { kind, origins, filtered } } = recipes;
  return (
    <main>
      <FloatingLabel
        controlId="floatingSelect"
        label="Selecione um local de origem"
      >
        <Form.Select
          data-testid="explore-by-area-dropdown"
          value={ origin }
          onChange={ handleOrigin }
        >
          { origins.map((ori, index) => (
            <option
              data-testid={ `${ori}-option` }
              key={ index }
              value={ ori }
            >
              {ori}
            </option>
          ))}
        </Form.Select>
      </FloatingLabel>
      <Cards kind={ kind } array={ [...filtered] } />

    </main>
  );
}

export default OriginExplorer;
