import React, { useContext } from 'react';
import ButtonGroup from 'react-bootstrap/ButtonGroup';
import ToggleButton from 'react-bootstrap/ToggleButton';
import RecipesContext from '../context/RecipesContext';

function Categories() {
  const { recipes, category, handleCategory } = useContext(RecipesContext);
  const { token } = recipes;
  const { [token]: { categories } } = recipes;

  return (
    <nav>
      <ButtonGroup
        className="mb-2"
        size="sm"
        type="checkbox"
        name="categorias"
        value={ category }
        onChange={ handleCategory }
      >
        <ToggleButton
          id="tbg-btg-All"
          data-testid="All-category-filter"
          size="sm"
          type="checkbox"
          variant="outline-danger"
          name="categorias"
          value="All"
          checked={ category === 'All' }
        >
          All
        </ToggleButton>
        { categories.map((cat, index) => (
          <ToggleButton
            key={ index }
            id={ `tbg-btg-${cat}` }
            data-testid={ `${cat}-category-filter` }
            size="sm"
            type="checkbox"
            variant="outline-dark"
            name="categorias"
            value={ cat }
            checked={ category === cat }
          >
            { cat }
          </ToggleButton>
        ))}
      </ButtonGroup>
    </nav>

  );
}

export default Categories;
