import React, { useContext } from 'react';
import Carousel from 'react-bootstrap/Carousel';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import Card from 'react-bootstrap/Card';

import RecipesContext from '../context/RecipesContext';
import { DRINKS_TOKEN, MEALS_TOKEN } from '../reducers/recipesReducer';

const SIX = 6;

function CarouselChild() {
  const { recipes } = useContext(RecipesContext);
  let { token } = recipes;
  let filtered;
  let kind;
  if (token === MEALS_TOKEN) {
    token = DRINKS_TOKEN;
    filtered = recipes[token].filtered;
    kind = recipes[token].kind;
  } else {
    token = MEALS_TOKEN;
    filtered = recipes[token].filtered;
    kind = recipes[token].kind;
  }

  const recomendations = [...filtered];
  recomendations.splice(SIX);
  const mockLint = true;
  return (
    <Carousel
      interval={ null }
    >
      { recomendations.reduce((acc, obj, index, array) => {
        if (index % 2 === 1) {
          const slides = (
            <Carousel.Item
              key={ acc.length }
              data-testid={ `${acc.length}-carousel-item` }
            >
              <Container>
                <Row>
                  <Col>
                    <Card
                      data-testid={ `${index - 1}-recomendation-card` }
                      text="dark"
                      border={
                        token === MEALS_TOKEN
                          ? 'warning'
                          : 'success'
                      }
                    >
                      { mockLint && (
                        <>
                          <Card.Img
                            src={ array[index - 1][`str${kind}Thumb`] }
                            alt={ array[index - 1][`str${kind}`] }
                          />
                          <Card.Header>
                            {array[index - 1].strCategory}
                          </Card.Header>
                          <Card.Body>
                            <Card.Title
                              data-testid={ `${index - 1}-recomendation-title` }
                            >
                              { array[index - 1][`str${kind}`]}
                            </Card.Title>

                          </Card.Body>
                        </>
                      )}
                    </Card>
                  </Col>
                  <Col>
                    <Card
                      data-testid={ `${index}-recomendation-card` }
                      text="dark"
                      border={
                        token === MEALS_TOKEN
                          ? 'warning'
                          : 'success'
                      }
                    >
                      { mockLint && (
                        <>
                          <Card.Img
                            src={ obj[`str${kind}Thumb`] }
                            alt={ obj[`str${kind}`] }
                          />
                          <Card.Header>
                            {obj.strCategory}
                          </Card.Header>
                          <Card.Body>
                            <Card.Title
                              data-testid={ `${index}-recomendation-title` }
                            >
                              { obj[`str${kind}`]}
                            </Card.Title>
                          </Card.Body>
                        </>
                      )}
                    </Card>

                  </Col>
                </Row>
              </Container>
            </Carousel.Item>
          );
          acc.push(slides);
        }
        return acc;
      }, [])}
    </Carousel>

  );
}

export default CarouselChild;
