import React, { useContext, useEffect } from 'react';
import { useHistory } from 'react-router-dom';
import PropTypes from 'prop-types';

import Stack from 'react-bootstrap/Stack';

import Button from 'react-bootstrap/Button';
import Alert from 'react-bootstrap/Alert';
import Footer from '../components/Footer';
import Header from '../components/Header';
import IngredientsExplorer from '../components/IngredientsExplorer';
import OriginExplorer from '../components/OriginExplorer';

import RecipesContext from '../context/RecipesContext';

import { TOKEN, MEALS_TOKEN, DRINKS_TOKEN } from '../reducers/recipesReducer';

function Explorer({ title, hasBtn, location, layer }) {
  const { recipes, dispatch, setIsItRandom, requestRandom } = useContext(RecipesContext);

  const { token } = recipes;
  const { [token]: { kind, route, recipe } } = recipes;

  const history = useHistory();
  // Necessário puxar o location pelas props porque no ambiente do Cypress
  // o pathname não é lido da mesma forma que o npm start
  const { pathname } = location;

  const handleClick = ({ target }) => {
    const { name, value } = target;
    if (name === MEALS_TOKEN || name === DRINKS_TOKEN) {
      dispatch({ type: TOKEN, token: name });
      history.push(`/explorar/${value}`);
    }
    if (name === 'ingredientes') {
      history.push(`${pathname}/${value}`);
    }
    if (name === 'area') {
      history.push(`${pathname}/${value}`);
    }
    if (name === 'surpreenda') {
      requestRandom();
      setIsItRandom(true);
    }
  };

  useEffect(() => {
    if (recipe[`id${kind}`]) {
      const id = recipe[`id${kind}`];
      history.push(`/${route}/${id}`);
    }
    // eslint-disable-next-line
  }, [recipe]);

  return (
    <main>
      <Header title={ title } hasBtn={ hasBtn } />
      {/* { (!pathname.includes('bebidas') && (!pathname.includes('comidas'))) && ( */}
      { (layer === 'initial' || layer === 'notfound') && (
        <Stack gap={ 2 } className="col-md-5 mx-auto">
          <Button
            data-testid="explore-food"
            type="button"
            name={ MEALS_TOKEN }
            value="comidas"
            variant="warning"
            onClick={ handleClick }
          >
            Explorar Comidas
          </Button>
          <Button
            data-testid="explore-drinks"
            type="button"
            name={ DRINKS_TOKEN }
            value="bebidas"
            variant="success"
            onClick={ handleClick }
          >
            Explorar Bebidas
          </Button>
        </Stack>
      )}
      {/* { (pathname.includes('bebidas') || pathname.includes('comidas')) && ( */}
      { layer === 'recipes' && (
        <Stack gap={ 2 } className="col-md-5 mx-auto">
          <Button
            data-testid="explore-by-ingredient"
            type="button"
            name="ingredientes"
            value="ingredientes"
            variant="secondary"
            onClick={ handleClick }
          >
            Por Ingredientes
          </Button>
          { pathname.includes('comidas') && (
            <Button
              data-testid="explore-by-area"
              type="button"
              name="area"
              value="area"
              variant="secondary"
              onClick={ handleClick }
            >
              Por Local de Origem
            </Button>
          ) }
          <Button
            data-testid="explore-surprise"
            type="button"
            name="surpreenda"
            value="random"
            variant="danger"
            onClick={ handleClick }
          >
            Me Surpreenda!
          </Button>
        </Stack>
      ) }
      { layer === 'ingredients' && <IngredientsExplorer /> }
      { layer === 'origin' && <OriginExplorer /> }
      { layer === 'notfound' && (
        <Alert variant="danger">
          <Alert.Heading>
            Not Found
          </Alert.Heading>
          <p>
            Não há como pesquisar Bebidas por Origem. Escolha outra opção.
          </p>

        </Alert>
      )}
      <Footer />
    </main>
  );
}

Explorer.defaultProps = {
  hasBtn: true,
};

Explorer.propTypes = {
  title: PropTypes.string.isRequired,
  hasBtn: PropTypes.bool,
  location: PropTypes.objectOf(PropTypes.any).isRequired,
  layer: PropTypes.string.isRequired,
};

export default Explorer;
