export const RECIPES_INITIAL_STATE = {
  meals: {
    kind: 'Meal',
    route: 'comidas',
    data: [],
    filtered: [],
    filter: '',
    search: [],
    categories: [],
    ingredients: [],
    origins: [],
    recipe: {},
    recipeInProgress: {},
    explorer: [],
    searched: false,
  },
  drinks: {
    kind: 'Drink',
    route: 'bebidas',
    data: [],
    filtered: [],
    filter: '',
    search: [],
    categories: [],
    ingredients: [],
    recipe: {},
    recipeInProgress: {},
    explorer: [],
    searched: false,
  },
  token: 'meals',
};

export const TOKEN = 'TOKEN';
export const ADD = 'ADD';
export const CATEGORIES = 'CATEGORIES';
export const INGREDIENTS = 'INGREDIENTS';
export const ORIGINS = 'ORIGINS';
export const SEARCH = 'SEARCH';
export const DISABLE_SEARCH = 'TOGLLE-SEARCH';
export const SET_FILTER = 'SET-FILTER';
export const RESET_FILTER = 'RESET-FILTER';
export const CLEAR_FILTER = 'CLEAR_FILTER';
export const MEALS_TOKEN = 'meals';
export const DRINKS_TOKEN = 'drinks';
export const RECIPE = 'RECIPE';

const recipesManager = (state, action) => {
  const { token } = state;
  switch (action.type) {
  case TOKEN:
    return {
      ...state,
      token: action.token,
      [action.token]: { ...state[action.token], searched: false },
    };
  case ADD:
    return {
      ...state,
      [action.token]: {
        ...state[action.token],
        data: action.payload,
        filtered: action.payload },
    };
  case RECIPE:
    return {
      ...state, [token]: { ...state[token], recipe: action.payload },
    };
  case CATEGORIES:
    return {
      ...state,
      [action.token]: {
        ...state[action.token],
        categories: action.payload },
    };
  case INGREDIENTS:
    return {
      ...state,
      [action.token]: {
        ...state[action.token],
        ingredients: action.payload },
    };
  case ORIGINS:
    return {
      ...state,
      [action.token]: {
        ...state[action.token],
        origins: action.payload },
    };
  case SEARCH:
    return {
      ...state, [token]: { ...state[token], search: action.payload, searched: true },
    };
  default: return state;
  }
};

const utilityManager = (state, action) => {
  const { token } = state;
  const { [token]: { data } } = state;
  switch (action.type) {
  case DISABLE_SEARCH:
    return {
      ...state,
      [token]: { ...state[token], searched: false },
    };
  case SET_FILTER:
    return {
      ...state,
      [token]: {
        ...state[token],
        filtered: action.payload.filtered,
        filter: action.payload.type,
      },
    };
  case RESET_FILTER:
    return {
      ...state,
      [token]: { ...state[token], filtered: data, filter: '' },
    };
  case CLEAR_FILTER:
    return {
      ...state,
      [token]: { ...state[token], filter: '' },
    };
  default:
    return state;
  }
};

export const recipesReducer = (state0, action) => {
  const state1 = recipesManager(state0, action);
  const state2 = utilityManager(state1, action);
  return state2;
};
