import React, { useContext, useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import PropTypes from 'prop-types';

import Figure from 'react-bootstrap/Figure';
import ListGroup from 'react-bootstrap/ListGroup';
import Accordion from 'react-bootstrap/Accordion';
import Button from 'react-bootstrap/Button';

import RecipesContext from '../context/RecipesContext';
import useRecipeMapping from '../hooks/useRecipeMapping';

import FavoriteButton from '../components/FavoriteButton';
import ShareButton from '../components/ShareButton';
import Instructions from '../components/Instructions';
import CarouselChild from '../components/CarouselChild';

import { MEALS_TOKEN } from '../reducers/recipesReducer';

const BTNS_INITITAL_STATE = {
  hadRecipe: false,
  isItInProgress: false,
  isItFavorite: false,
};

function RecipeDetails({ match }) {
  const [btnsState, setBtnsState] = useState(BTNS_INITITAL_STATE);
  const {
    recipes,
    requestRecipe,
    recipesLS,
    isItRandom,
    setIsItRandom,
  } = useContext(RecipesContext);
  const [recipeMapped, recipeMapping] = useRecipeMapping([]);
  const history = useHistory();
  const { location: { pathname } } = history;

  const { token } = recipes;
  const { [token]: { recipe, kind, route } } = recipes;

  useEffect(() => {
    if (pathname.includes(route)) {
      if (isItRandom) {
        setIsItRandom(false);
      } else {
        const { params: { id } } = match;
        requestRecipe(id);
      }
    }
  // eslint-disable-next-line
  }, [token]);

  useEffect(() => {
    const { doneRecipes, inProgressRecipes, favoriteRecipes } = recipesLS;
    const inProgressRecipesTokened = { ...inProgressRecipes[token] };
    const hadRecipe = doneRecipes
      .some(({ id }) => Number(id) === Number(recipe[`id${kind}`]));
    const isItInProgress = Object.keys(inProgressRecipesTokened)
      .some((id) => Number(id) === Number(recipe[`id${kind}`]));
    const isItFavorite = favoriteRecipes
      .some(({ id }) => Number(id) === Number(recipe[`id${kind}`]));
    setBtnsState({ hadRecipe, isItInProgress, isItFavorite });
    // eslint-disable-next-line
  }, [recipesLS, recipe, token, kind]);

  const { hadRecipe, isItInProgress, isItFavorite } = btnsState;
  const { url } = match;

  useEffect(() => {
    recipeMapping(recipe);
    // eslint-disable-next-line
  }, [recipe]);

  return (
    <section>
      { recipeMapped.length > 0 && recipeMapped.map((rec, index) => (
        <div key={ index }>
          <Figure>
            <Figure.Image
              data-testid="recipe-photo"
              src={ rec[`str${kind}Thumb`] }
              width="360"
              height="50"
              alt={ rec[`str${kind}`] }
            />
          </Figure>
          <ShareButton
            url={ url }
          />
          <FavoriteButton
            recipe={ rec }
            isItFavorite={ isItFavorite }
          />
          <h2 data-testid="recipe-title">{ rec[`str${kind}`] }</h2>
          <h6 data-testid="recipe-category">
            {
              kind === 'Drink'
                ? `${rec.strCategory} ${rec.strAlcoholic}`
                : rec.strCategory
            }
          </h6>
          <Accordion>
            <Accordion.Item eventKey="0">
              <Accordion.Header>Ingredients</Accordion.Header>
              <Accordion.Body data-testid="ingredients">
                <ListGroup size="sm">
                  { rec.ingredients.map((ing, idx) => (
                    <ListGroup.Item
                      size="sm"
                      key={ idx }
                      data-testid={ `${idx}-ingredient-name-and-measure` }
                    >
                      { `${ing} ${rec.measures[idx]}` }
                    </ListGroup.Item>
                  ))}
                </ListGroup>
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>
          <Instructions instructions={ rec.strInstructions } />
          { token === MEALS_TOKEN && (
            <iframe
              width="360"
              height="202"
              data-testid="video"
              src={ rec.strYoutube }
              title="YouTube video player"
              frameBorder="0"
              allow={ `accelerometer; controls=1; showinfo=0; clipboard-write;
              encrypted-media; gyroscope; picture-in-picture` }
              allowFullScreen
            />
          )}
        </div>
      ))}
      <CarouselChild />
      { recipeMapped.length > 0 && (
        <div className="d-grid gap-2">
          <Button
            size="lg"
            hidden={ hadRecipe }
            data-testid="start-recipe-btn"
            variant="warning"
            type="button"
            style={ { position: 'fixed', bottom: 0 } }
            onClick={ () => {
              history.push(`/${route}/${recipeMapped[0][`id${kind}`]}/in-progress`);
            } }
          >
            { isItInProgress ? 'Continuar Receita' : 'Iniciar Receita' }
          </Button>
        </div>) }
    </section>
  );
}

RecipeDetails.propTypes = {
  match: PropTypes.objectOf(PropTypes.any).isRequired,
};

export default RecipeDetails;
