import React from 'react';
import PropTypes from 'prop-types';
import { Link } from 'react-router-dom';

import RecipesContentCard from './RecipesContentCard';

function RecipesDetails({ recipes, group }) {
  return (
    <section>
      { recipes.map((recipe, index) => (
        <div
          key={ index }
          className={ recipe.type === 'comida'
            ? 'card mb-2 border-warning'
            : 'card mb-2 border-success' }
          bg="light"
        >
          <div className="card-body">
            <Link to={ `/${recipe.type}s/${recipe.id}` }>
              <h5
                className="card-title"
                data-testid={ `${index}-horizontal-name` }
                style={ { color: 'rgb(0,0,0)' } }
              >
                { recipe.name }
              </h5>
            </Link>
            {/* foi necessário fazer manualmente o card por causa da linha abaixo;
            com o componente Card do React Bootstrap ocorria uma tag <p> como
            pai de RecipesContentCard */}
            <div className="card-text">
              <RecipesContentCard
                recipe={ recipe }
                group={ group }
                index={ index }
              />
            </div>
          </div>
        </div>
      ))}
    </section>
  );
}

RecipesDetails.propTypes = {
  recipes: PropTypes.arrayOf(PropTypes.any).isRequired,
  group: PropTypes.string.isRequired,
};

export default RecipesDetails;
