import React, { useContext } from 'react';
import { Redirect } from 'react-router';
import PropTypes from 'prop-types';
import Header from '../components/Header';
import Cards from '../components/Cards';
import RecipesContext from '../context/RecipesContext';
import Footer from '../components/Footer';
import Categories from '../components/Categories';

function Recipes({ title }) {
  const { recipes } = useContext(RecipesContext);
  const { token } = recipes;
  const { [token]: { kind, route, filtered, search, searched, categories } } = recipes;

  if (searched && search.length === 0) {
    global.alert('Sinto muito, não encontramos nenhuma receita para esses filtros.');
  }
  return (
    <main>
      <Header title={ title } />
      { categories.length > 0 && <Categories />}
      { searched && search.length === 1
        && <Redirect to={ `/${route}/${search[0][`id${kind}`]}` } />}
      { searched && search.length > 0
        && <Cards kind={ kind } array={ [...search] } /> }
      { !searched && filtered.length > 0
        && <Cards kind={ kind } array={ [...filtered] } /> }
      <Footer />
    </main>
  );
}

Recipes.propTypes = {
  title: PropTypes.string.isRequired,
};

export default Recipes;
