import React, { useContext, useEffect } from 'react';
import { useHistory } from 'react-router-dom';

import Card from 'react-bootstrap/Card';

import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';

import RecipesContext from '../context/RecipesContext';
import { CLEAR_FILTER, MEALS_TOKEN } from '../reducers/recipesReducer';

function IngredientsExplorer() {
  const { recipes, handleIngredient, dispatch } = useContext(RecipesContext);
  const history = useHistory();
  const { token } = recipes;
  const { [token]: { ingredients, filter, route } } = recipes;

  useEffect(() => {
    if (filter === 'ingredient') {
      dispatch({ type: CLEAR_FILTER });
      history.push(`/${route}`);
    }
  }, [filter, route, dispatch, history]);

  return (
    <main>
      <Container fluid>
        { ingredients.reduce((acc, ing, index, array) => {
          const { strIngredient: ingredient } = ing;
          if (index % 2 === 1) {
            const ingrEven = `${array[index - 1].strIngredient}-Small.png`;
            const imageEven = token === MEALS_TOKEN
              ? `https://www.themealdb.com/images/ingredients/${ingrEven}`
              : `https://www.thecocktaildb.com/images/ingredients/${ingrEven}`;
            const ingrOdd = `${ingredient}-Small.png`;
            const imageOdd = token === MEALS_TOKEN
              ? `https://www.themealdb.com/images/ingredients/${ingrOdd}`
              : `https://www.thecocktaildb.com/images/ingredients/${ingrOdd}`;
            const result = (
              <Row key={ acc.length }>
                <Col>
                  <Card
                    data-testid={ `${index - 1}-ingredient-card` }
                    border={ token === MEALS_TOKEN ? 'warning' : 'success' }
                    text="dark"
                    style={ { width: '9rem' } }
                    onClick={ () => handleIngredient(array[index - 1].strIngredient) }
                  >
                    <Card.Img
                      data-testid={ `${index - 1}-card-img` }
                      src={ imageEven }
                      alt={ `Ingrediente ${array[index - 1].strIngredient}` }
                      variant="top"
                    />
                    <Card.Body>
                      <Card.Title
                        data-testid={ `${index - 1}-card-name` }
                      >
                        {array[index - 1].strIngredient}
                      </Card.Title>
                    </Card.Body>
                  </Card>
                </Col>
                <Col>
                  <Card
                    data-testid={ `${index}-ingredient-card` }
                    border={ token === MEALS_TOKEN ? 'warning' : 'success' }
                    text="dark"
                    style={ { width: '9rem' } }
                    onClick={ () => handleIngredient(ingredient) }
                  >
                    <Card.Img
                      data-testid={ `${index}-card-img` }
                      src={ imageOdd }
                      alt={ `Ingrediente ${ingredient}` }
                      variant="top"
                    />
                    <Card.Body
                      data-testid={ `${index}-card-name` }
                    >
                      <Card.Title>
                        {ingredient}
                      </Card.Title>
                    </Card.Body>
                  </Card>
                </Col>
              </Row>
            );
            acc.push(result);
          }
          return acc;
        }, [])}
      </Container>
    </main>
  );
}

export default IngredientsExplorer;
