import { useState, useCallback } from 'react';

const reduceFn = (acc, key, value) => {
  if (!key.includes('Ingredient') && !key.includes('Measure')) {
    acc.push([key, value]);
    return acc;
  } if (key.includes('Ingredient')) {
    const index = acc.findIndex(([accKeys]) => accKeys.includes('ingredients'));
    if (index < 0) {
      acc.push(['ingredients', [value]]);
      return acc;
    }
    const [ingr, ingrArray] = acc[index];
    ingrArray.push(value);
    acc[index] = [ingr, ingrArray];
    return acc;
  } if (key.includes('Measure')) {
    const index = acc.findIndex(([accKeys]) => accKeys.includes('measures'));
    if (index < 0) {
      acc.push(['measures', [value]]);
      return acc;
    }
    const [meas, measArray] = acc[index];
    measArray.push(value);
    acc[index] = [meas, measArray];
    return acc;
  }
  return acc;
};

function useRecipeMapping(inititalState = []) {
  const [recipeMapped, setRecipeMapped] = useState(inititalState);

  // eslint-disable-next-line
  const recipeMapping = useCallback((recipe) => {
    if (Object.keys(recipe).length > 0) {
      const entries = Object.entries(recipe);
      const recipeFiltered = entries
        .filter(([key, value]) => key && value !== '' && value !== ' ' && value !== null);
      const recipeRearranged = recipeFiltered
        .reduce((acc, [key, value]) => reduceFn(acc, key, value), []);
      const newEntries = new Map(recipeRearranged);
      const recipeObject = Object.fromEntries(newEntries);
      setRecipeMapped([recipeObject]);
    } else {
      setRecipeMapped([]);
    }
  });

  return [recipeMapped, recipeMapping];
}

export default useRecipeMapping;
