import React, { useContext } from 'react';
import PropTypes from 'prop-types';

import Button from 'react-bootstrap/Button';
import RecipesContext from '../context/RecipesContext';
import whiteHeartIcon from '../images/whiteHeartIcon.svg';
import blackHeartIcon from '../images/blackHeartIcon.svg';

import { DRINKS_TOKEN, MEALS_TOKEN } from '../reducers/recipesReducer';

import {
  saveForiteRecipesLS,
  getFromLocalStorage,
  removeFavoriteRecipeLS } from '../services';

function FavoriteButton({ recipe, isItFavorite, testid, group }) {
  const { recipes, setRecipesLS } = useContext(RecipesContext);
  const { token } = recipes;
  const { [token]: { kind } } = recipes;

  const handleFavorite = (obj) => {
    if (group) {
      removeFavoriteRecipeLS(obj.id);
    } else if (isItFavorite) {
      removeFavoriteRecipeLS(obj[`id${kind}`]);
    } else {
      const id = obj[`id${kind}`];
      const name = obj[`str${kind}`];
      const image = obj[`str${kind}Thumb`];
      const alcoholicOrNot = token === DRINKS_TOKEN ? obj.strAlcoholic : '';
      const type = token === MEALS_TOKEN ? 'comida' : 'bebida';
      const area = token === MEALS_TOKEN ? obj.strArea : '';
      const { strCategory: category } = obj;
      const newObj = { id, type, area, category, alcoholicOrNot, name, image };
      saveForiteRecipesLS(newObj);
    }
    setRecipesLS(getFromLocalStorage());
  };

  return (
    <Button
      variant="light"
      type="button"
      onClick={ () => handleFavorite(recipe) }
    >
      <img
        data-testid={ testid }
        src={ isItFavorite ? blackHeartIcon : whiteHeartIcon }
        alt="Bookmark this"
      />
    </Button>
  );
}

FavoriteButton.defaultProps = {
  testid: 'favorite-btn',
  group: '',
};

FavoriteButton.propTypes = {
  recipe: PropTypes.objectOf(PropTypes.any).isRequired,
  isItFavorite: PropTypes.bool.isRequired,
  testid: PropTypes.string,
  group: PropTypes.string,
};

export default FavoriteButton;
