import React, { useCallback, useReducer, useState, useEffect } from 'react';
import PropTypes from 'prop-types';
import RecipesContext from './RecipesContext';
import { getFromLocalStorage } from '../services';
import {
  recipesReducer, RECIPES_INITIAL_STATE,
} from '../reducers/recipesReducer';

import useRequest from '../hooks/useRequest';
import usePrevious from '../hooks/usePrevious';

function RecipesProvider({ children }) {
  const [recipes, dispatch] = useReducer(recipesReducer, RECIPES_INITIAL_STATE);
  const [recipesLS, setRecipesLS] = useState(getFromLocalStorage());
  const [status, requestApi] = useRequest(dispatch);
  const [category, setCategory] = useState('All');
  const prevCategory = usePrevious(category);
  const [origin, setOrigin] = useState('All');
  const prevOrigin = usePrevious(origin);
  const [ingredient, setIngredient] = useState('All');
  const prevIngredient = usePrevious(ingredient);
  const [isItRandom, setIsItRandom] = useState(false);

  const searchRecipes = useCallback(
    (token, option, text) => {
      requestApi({ request: 'SEARCH', options: { token, option, text } });
    }, [requestApi],
  );

  const { token } = recipes;
  const { [token]: { filter } } = recipes;

  const requestRecipe = useCallback(
    (id) => {
      requestApi({ request: 'RECIPE', options: { token, id } });
    }, [token, requestApi],
  );

  const requestRandom = useCallback(
    () => {
      requestApi({ request: 'RANDOM', options: { token } });
    }, [token, requestApi],
  );

  useEffect(() => {
    if (prevCategory !== category) {
      requestApi({ request: 'CATEGORY', options: { token, category } });
    }
    // eslint-disable-next-line
  }, [category, requestApi, token]);

  useEffect(() => {
    if (prevOrigin !== origin) {
      requestApi({ request: 'ORIGIN', options: { token, origin } });
    }
    // eslint-disable-next-line
  }, [origin, requestApi, token]);

  useEffect(() => {
    if (prevIngredient !== ingredient) {
      if (ingredient !== 'All' && filter !== 'ingredient') {
        requestApi({ request: 'INGREDIENT', options: { token, ingredient } });
      }
      if (filter === 'ingredient') {
        setIngredient('All');
      }
    }
    // eslint-disable-next-line
  }, [ingredient, filter, requestApi, token]);

  useEffect(() => {
    requestApi({ request: 'INITITAL' });
    // eslint-disable-next-line
  }, []);

  const handleCategory = ({ target: { value } }) => {
    if (category !== value) {
      setCategory(value);
    } else {
      setCategory('All');
    }
  };

  const handleOrigin = ({ target: { value } }) => {
    if (origin !== value) {
      setOrigin(value);
    } else {
      setOrigin('All');
    }
  };

  const handleIngredient = (ingrd) => {
    setIngredient(ingrd);
  };

  const context = {
    status,
    recipes,
    recipesLS,
    category,
    origin,
    isItRandom,
    setRecipesLS,
    setIsItRandom,
    dispatch,
    searchRecipes,
    requestRecipe,
    requestRandom,
    handleOrigin,
    handleCategory,
    handleIngredient,
  };

  return (
    <RecipesContext.Provider value={ context }>
      { children }
    </RecipesContext.Provider>
  );
}

RecipesProvider.propTypes = {
  children: PropTypes.node.isRequired,
};

export default RecipesProvider;
