import React, { useContext, useEffect, useState } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';

import Figure from 'react-bootstrap/Figure';
import ListGroup from 'react-bootstrap/ListGroup';
import Accordion from 'react-bootstrap/Accordion';
import Button from 'react-bootstrap/Button';

import useRecipeMapping from '../hooks/useRecipeMapping';
import RecipesContext from '../context/RecipesContext';

import FavoriteButton from '../components/FavoriteButton';
import ShareButton from '../components/ShareButton';
import Instructions from '../components/Instructions';

import { saveInProgressRecipesLS, getFromLocalStorage } from '../services';

const INITITAL_STATE = {
  notFinished: true,
  isItInProgress: false,
  isItFavorite: false,
};

const STYLE_CROSSED = {
  textDecoration: 'line-through solid rgb(0,0,0)',
};

const STYLE_NORMAL = {
  textDecoration: 'none solid rgb(0,0,0)',
};

function RecipeInProgress({ match }) {
  const [inProgressState, setInProgressState] = useState(INITITAL_STATE);
  const [steps, setSteps] = useState({});
  const [recipeMapped, recipeMapping] = useRecipeMapping([]);

  const { recipes, requestRecipe, recipesLS, setRecipesLS } = useContext(RecipesContext);
  const { token } = recipes;
  const { [token]: { kind, recipe, route } } = recipes;

  useEffect(() => {
    const { params: { id } } = match;
    requestRecipe(id);
    // eslint-disable-next-line
  }, [requestRecipe]);

  useEffect(() => {
    recipeMapping(recipe);
    // eslint-disable-next-line
  }, [recipe]);

  useEffect(() => {
    if (recipeMapped.length > 0) {
      const { ingredients } = recipeMapped[0];
      const { params: { id } } = match;
      const entries = Object.entries(steps);
      const result = entries.reduce((acc, [key, value]) => {
        if (value) {
          acc.push(`${key}`);
        }
        return acc;
      }, []);
      const obj = { id, ingredientes: result };
      saveInProgressRecipesLS(obj, token);
      setRecipesLS(getFromLocalStorage());
      if (result.length === ingredients.length) {
        setInProgressState({ ...inProgressState, notFinished: false });
      } else {
        setInProgressState({ ...inProgressState, notFinished: true });
      }
    }
    // eslint-disable-next-line
  }, [steps, token, recipeMapped]);

  useEffect(() => {
    const { params: { id } } = match;
    const { inProgressRecipes } = getFromLocalStorage();
    const inProgressRecipesTokened = { ...inProgressRecipes[token] };
    if (inProgressRecipesTokened[id]) {
      const ingredientes = inProgressRecipesTokened[id];
      const entries = ingredientes.map((ingrediente) => [ingrediente, true]);
      const obj = Object.fromEntries(entries);
      setSteps(obj);
    }
    // eslint-disable-next-line
  }, [token]);

  useEffect(() => {
    const { inProgressRecipes, favoriteRecipes } = recipesLS;
    const inProgressRecipesTokened = { ...inProgressRecipes[token] };
    const isItInProgress = Object.keys(inProgressRecipesTokened)
      .some((id) => Number(id) === Number(recipe[`id${kind}`]));
    const isItFavorite = favoriteRecipes
      .some(({ id }) => Number(id) === Number(recipe[`id${kind}`]));
    setInProgressState({ ...inProgressState, isItInProgress, isItFavorite });
    // eslint-disable-next-line
  }, [recipesLS, recipe, token, kind]);

  const { notFinished, isItFavorite } = inProgressState;
  const { params: { id } } = match;

  const handleChange = ({ target }) => {
    const { name, checked } = target;
    setSteps({ ...steps, [name]: checked });
  };

  return (
    <section>
      { recipeMapped.length > 0 && recipeMapped.map((rec, index) => (
        <div key={ index }>
          <Figure>
            <Figure.Image
              data-testid="recipe-photo"
              src={ rec[`str${kind}Thumb`] }
              width="360"
              height="50"
              alt={ rec[`str${kind}`] }
            />
          </Figure>
          <ShareButton
            url={ `/${route}/${id}` }
          />
          <FavoriteButton
            recipe={ rec }
            isItFavorite={ isItFavorite }
          />
          <h2 data-testid="recipe-title">{ rec[`str${kind}`] }</h2>
          <h6 data-testid="recipe-category">
            {
              kind === 'Drink'
                ? `${rec.strCategory} ${rec.strAlcoholic}`
                : rec.strCategory
            }
          </h6>
          <Accordion defaultActiveKey="0">
            <Accordion.Item eventKey="0">
              <Accordion.Header>Ingredients</Accordion.Header>
              <Accordion.Body data-testid="ingredients">
                <ListGroup>
                  { rec.ingredients.map((ing, idx) => {
                    const string = `${ing} - ${rec.measures[idx]}`;
                    return (
                      <ListGroup.Item key={ idx } variant="light" as="li">
                        <div className="form-check">
                          <label
                            className="form-check-label"
                            htmlFor={ `${idx}-ingredient-step` }
                            data-testid={ `${idx}-ingredient-step` }
                            style={
                              steps[string]
                                ? STYLE_CROSSED
                                : STYLE_NORMAL
                            }
                          >
                            { string }
                            <input
                              id={ `${idx}-ingredient-step` }
                              name={ string }
                              checked={ steps[string] }
                              className="form-check-input"
                              type="checkbox"
                              onChange={ handleChange }
                            />
                          </label>
                        </div>
                      </ListGroup.Item>
                    );
                  })}
                </ListGroup>
              </Accordion.Body>
            </Accordion.Item>
          </Accordion>
          <Instructions instructions={ rec.strInstructions } />
        </div>
      ))}

      <div className="d-grid gap-2">
        <Link
          to="/receitas-feitas"
        >
          <Button
            size="lg"
            disabled={ notFinished }
            data-testid="finish-recipe-btn"
            variant="success"
            type="button"
            style={ { position: 'fixed', bottom: 0 } }
          >
            Finalizar receita
          </Button>
        </Link>
      </div>
    </section>
  );
}

RecipeInProgress.propTypes = {
  match: PropTypes.objectOf(PropTypes.any).isRequired,
};

export default RecipeInProgress;
