import React, { useContext, useState } from 'react';
import { Link } from 'react-router-dom';
import PropTypes from 'prop-types';
import Form from 'react-bootstrap/Form';
import FloatingLabel from 'react-bootstrap/FloatingLabel';
import Button from 'react-bootstrap/Button';
import profileIcon from '../images/profileIcon.svg';
import searchIcon from '../images/searchIcon.svg';
import RecipesContext from '../context/RecipesContext';
import { DISABLE_SEARCH } from '../reducers/recipesReducer';

const RADIO_VALUES = ['ingredient', 'name', 'first-letter'];
const RADIO_LABELS = ['Ingrediente', 'Nome', 'Primeira letra'];

function Header({ title, hasBtn }) {
  const [hasSearchBar, setHasSearchBar] = useState(false);
  const [search, setSearch] = useState({ text: '', option: '' });
  const { searchRecipes, recipes: { token }, dispatch } = useContext(RecipesContext);
  const { text, option } = search;

  const handleChange = ({ target: { name, value } }) => {
    setSearch({ ...search, [name]: value });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    if (option === 'first-letter' && text.length > 1) {
      global.alert('Sua busca deve conter somente 1 (um) caracter');
    } else {
      searchRecipes(token, option, text);
    }
  };

  const handleClick = () => {
    setHasSearchBar(!hasSearchBar);
    dispatch({ type: DISABLE_SEARCH });
  };

  return (
    <header>
      <h1 data-testid="page-title">
        <Link to="/perfil">
          <button type="button">
            <img src={ profileIcon } alt="Perfil" data-testid="profile-top-btn" />
          </button>
        </Link>
        { title }
        { hasBtn
        && (
          <button
            type="button"
            name="search-top-btn"
            onClick={ handleClick }
          >
            <img src={ searchIcon } alt="Pesquisar" data-testid="search-top-btn" />
          </button>
        )}
      </h1>
      { hasSearchBar
        && (
          <Form id="form-search" onSubmit={ handleSubmit }>
            <FloatingLabel
              label="Pesquisar por"
              className="mb-3"
              htmlFor="search_input"
            >
              <Form.Control
                data-testid="search-input"
                id="seach_input"
                type="text"
                name="text"
                value={ text }
                placeholder="Search for Meals"
                required
                onChange={ handleChange }
              />
              { RADIO_VALUES.map((radio, index) => (
                <Form.Check
                  key={ index }
                  id={ `radio_${radio}` }
                  data-testid={ `${radio}-search-radio` }
                  type="radio"
                  name="option"
                  value={ radio }
                  checked={ radio === option }
                  label={ RADIO_LABELS[index] }
                  required
                  onChange={ handleChange }
                />
              ))}
            </FloatingLabel>
            <Button
              type="submit"
              data-testid="exec-search-btn"
            >
              Buscar
            </Button>
          </Form>
        )}
    </header>
  );
}

Header.defaultProps = {
  hasBtn: true,
};

Header.propTypes = {
  title: PropTypes.string.isRequired,
  hasBtn: PropTypes.bool,
};

export default Header;
