import { useCallback, useState } from 'react';

import { fetchApi } from '../services';

import {
  ADD,
  SEARCH,
  MEALS_TOKEN,
  DRINKS_TOKEN,
  CATEGORIES,
  ORIGINS,
  SET_FILTER,
  RESET_FILTER,
  RECIPE,
  INGREDIENTS,
} from '../reducers/recipesReducer';

const STATUS_INITIAL_STATE = {
  error: {
    hasError: false,
    message: '',
  },
  isFetching: false,
  requested: false,
};

const TWELVE = 12;
const FIVE = 5;

const initialRequest = async (dispatch) => {
  {
    const { meals: list } = await fetchApi(MEALS_TOKEN, 'list');
    if (list.length > TWELVE) {
      list.splice(TWELVE);
    }
    dispatch({ type: ADD, token: MEALS_TOKEN, payload: list });
  }
  {
    const { drinks: list } = await fetchApi(DRINKS_TOKEN, 'list');
    if (list.length > TWELVE) {
      list.splice(TWELVE);
    }
    dispatch({ type: ADD, token: DRINKS_TOKEN, payload: list });
  }
  {
    const { meals: array } = await fetchApi(MEALS_TOKEN, 'categories');
    if (array.length > FIVE) {
      array.splice(FIVE);
    }
    const categories = array.map(({ strCategory }) => strCategory);
    dispatch({ type: CATEGORIES, token: MEALS_TOKEN, payload: categories });
  }
  {
    const { drinks: array } = await fetchApi(DRINKS_TOKEN, 'categories');
    if (array.length > FIVE) {
      array.splice(FIVE);
    }
    const categories = array.map(({ strCategory }) => strCategory);
    dispatch({ type: CATEGORIES, token: DRINKS_TOKEN, payload: categories });
  }
  {
    const { meals: ingredients } = await fetchApi(MEALS_TOKEN, 'ingredients');
    ingredients.splice(TWELVE);
    dispatch({ type: INGREDIENTS, token: MEALS_TOKEN, payload: ingredients });
  }
  {
    const { drinks: array } = await fetchApi(DRINKS_TOKEN, 'ingredients');
    const ingredients = array
      .map(({ strIngredient1 }) => ({ strIngredient: strIngredient1 }));
    ingredients.splice(TWELVE);
    dispatch({ type: INGREDIENTS, token: DRINKS_TOKEN, payload: ingredients });
  }
  {
    const { meals: array } = await fetchApi(MEALS_TOKEN, 'origins');
    const origins = array.map(({ strArea }) => strArea);
    dispatch({ type: ORIGINS, token: MEALS_TOKEN, payload: ['All', ...origins] });
  }
};

const requestBy = async (token, type, search, dispatch) => {
  if (search !== 'All') {
    const result = await fetchApi(token, type, search);
    const { [token]: array } = result;
    const filtered = array !== null ? [...array] : [];
    if (filtered.length > TWELVE) filtered.splice(TWELVE);
    dispatch({ type: SET_FILTER, payload: { filtered, type } });
  } else {
    dispatch({ type: RESET_FILTER });
  }
};

function useRequest(dispatch) {
  const [status, setStatus] = useState(STATUS_INITIAL_STATE);

  const requestApi = useCallback(
    async ({ request, options = {} }) => {
      try {
        setStatus({ ...status, isFetching: true, requested: false });
        switch (request) {
        case 'INITITAL': {
          await initialRequest(dispatch);
          break; }
        case 'SEARCH': {
          const { token, option, text } = options;
          const result = await fetchApi(token, option, text);
          const { [token]: array } = result;
          const search = array !== null ? [...array] : [];
          if (search.length > TWELVE) search.splice(TWELVE);
          dispatch({ type: SEARCH, payload: search });
          break; }
        case 'RECIPE': {
          const { token, id } = options;
          const result = await fetchApi(token, 'id', id);
          const { [token]: array } = result;
          dispatch({ type: RECIPE, payload: array[0] });
          break; }
        case 'RANDOM': {
          const { token } = options;
          const result = await fetchApi(token, 'random');
          const { [token]: array } = result;
          dispatch({ type: RECIPE, payload: array[0] });
          break; }
        case 'CATEGORY': {
          const { token, category } = options;
          requestBy(token, 'category', category, dispatch);
          break; }
        case 'ORIGIN': {
          const { token, origin } = options;
          requestBy(token, 'origin', origin, dispatch);
          break; }
        case 'INGREDIENT': {
          const { token, ingredient } = options;
          requestBy(token, 'ingredient', ingredient, dispatch);
          break; }
        default: break;
        }
        setStatus({
          error: { hasError: false, message: '' }, isFetching: false, requested: true });
      } catch (err) {
        setStatus({
          error: { hasError: true, message: err.message },
          isFetching: false,
          requested: true });
      }
    // eslint-disable-next-line
    }, [],
  );
  return [status, requestApi];
}

export default useRequest;
