import React from 'react';
import PropTypes from 'prop-types';

import ButtonGroup from 'react-bootstrap/ButtonGroup';
import ToggleButton from 'react-bootstrap/ToggleButton';

function FilterByKind({ type, handleFilter }) {
  return (
    <nav className="d-grid gap-2">
      <ButtonGroup
        className="mb-2"
        size="sm"
        type="checkbox"
        name="type"
        value={ type }
        onChange={ handleFilter }
      >
        <ToggleButton
          id="tbg-btg-all"
          size="sm"
          type="checkbox"
          variant="outline-danger"
          name="type"
          value="all"
          checked={ type === 'all' }
          data-testid="filter-by-all-btn"
        >
          All
        </ToggleButton>
        <ToggleButton
          id="tbg-btg-comida"
          size="sm"
          type="checkbox"
          variant="outline-warning"
          name="type"
          value="comida"
          checked={ type === 'comida' }
          data-testid="filter-by-food-btn"
        >
          Comidas
        </ToggleButton>
        <ToggleButton
          id="tbg-btg-bebida"
          size="sm"
          type="checkbox"
          variant="outline-success"
          name="type"
          value="bebida"
          checked={ type === 'bebida' }
          data-testid="filter-by-drink-btn"
        >
          Drinks
        </ToggleButton>
      </ButtonGroup>
    </nav>

  );
}

FilterByKind.propTypes = {
  type: PropTypes.string.isRequired,
  handleFilter: PropTypes.func.isRequired,
};

export default FilterByKind;
